<!-- v-model ==> 双向数据绑定 -->
<!-- 封装方法2: 此处可拆成 @input 和 :value  ==> 数据单向流动 -->
<template>
    <div style="border: 1px solid #ccc;">
        <Toolbar style="border-bottom: 1px solid #ccc" :editor="editor" :defaultConfig="toolbarConfig" :mode="mode" />
        <Editor style="height: 500px; overflow-y: hidden;" v-model="inner_content" :defaultConfig="editorConfig"
            :mode="mode" @onCreated="onCreated" />
    </div>
</template>

<script>
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
// 引入富文本编辑器style
import '@wangeditor/editor/dist/css/style.css';

// 处理 -- 表单验证
import { Boot } from '@wangeditor/editor';
// editor为空获取value值存在"<p><br></p>"，在表单中无法校验，借助于插件解决
function withEmptyHtml(editor) {
    const { getHtml } = editor
    const newEditor = editor

    newEditor.getHtml = () => {
        if (newEditor.isEmpty()) {
            return ''
        }
        return getHtml()
    }

    return newEditor
}
// 注册插件
Boot.registerPlugin(withEmptyHtml);

export default {
    components: {
        Editor,
        Toolbar,
    },
    model: {
        prop: 'content',
        event: 'change',
    },
    props: {
        content: {
            type: String,
            default: '',
        },
        mode: {
            type: String,
            default: 'default',
        }
    },
    data() {
        return {
            inner_content: '',// 内部转存一下
            editor: null,
            // 两个配置对象 -- wangeditor文档中有详细介绍
            toolbarConfig: {},
            editorConfig: {
                placeholder: '请输入内容...',
                // 配置富文本编辑器 -- 此处配置上传图片
                MENU_CONF: {
                    uploadImage: {
                        // 上传到的服务端地址
                        server: `${process.env.VUE_APP_SERVER}/upload/editor/`,
                        // 自定义增加 http  header
                        headers: {
                            Authorization: `Bearer ${sessionStorage.access_token}`,
                        },
                        // form-data fieldName ，默认值 'wangeditor-uploaded-image'
                        fieldName: 'file',
                    }
                },
            },
        }
    },
    methods: {
        onCreated(editor) { // 初始化富文本编辑器
            this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
        },
    },
    beforeDestroy() {
        const editor = this.editor
        if (editor == null) return
        editor.destroy() // 组件销毁时，及时销毁编辑器
    },
    watch: {
        'content': function (newValue, oldValue) {
            this.inner_content = newValue;
        },
        'inner_content': function (newValue, oldValue) {
            this.$emit('change', newValue);
        },
    }
}
</script>

<style lang="less" scoped></style>